
body {
  background: #ddd;
}
#root {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
}

.inputWrapper>input {
    width: 24em;
    height:3em;
    background: rgb(234, 234, 234);
    border: rgb(234, 234, 234) solid 1px;
    border-radius: 6px;
    padding-left: 2em;
    outline: none;
}

.inputWrapper>input::-webkit-input-placeholder{
    color: #929292;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;
    font-weight: 200;
  }

.inputWrapper>input::-ms-input-placeholder{
    color: #929292;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;
    font-weight: 200;
  }

.inputWrapper>input::placeholder{
    color: #929292;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;
    font-weight: 200;
  }

.App{
  color: #333;
  border: 1px solid #aaa;
  border-radius: 4px;
  background: white;
  margin: 2em;
  padding-bottom: 1.5em;
  min-height: 20em;
  min-width: 32em;
  text-align: center;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}

.App>header {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #FFFFFF;
  font-size: 12px;
  line-height: 32px;
  height: 32px;
  text-align: center;
  background-color: rgb(230, 46, 72);
  opacity: 0.8;
}

.App>.dashBoard {

  background: -webkit-linear-gradient(left,rgb(237, 132, 114),rgb(239, 147, 94));

  background: linear-gradient(90deg,rgb(237, 132, 114),rgb(239, 147, 94));
  opacity: 0.7;
  height: 10em;
  text-align: center;
  padding-top: 1.5em;
}
.dashBoard>p{
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: #FFFFFF; 
}

.App>.inputWrapper {
  margin: 1.5em;
}


.todoList>li>div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 1.5em;
    margin-bottom: 1.5em;
    width: 24em;
    height:2.5em;
    background: rgb(234, 234, 234);
    border: rgb(234, 234, 234) solid 1px;
    border-radius: 6px;
    margin-left: auto;
    margin-right: auto;
}

.todoList input{    
    height: 2.5em;
    width: 2.5em;
}
.itemList {
    color: #3B3838;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;
    font-weight: 200;
    position: relative;
}

.itemList label{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #929292;
    background: rgb(255,255,255);
    display: inline-block; 
    position: absolute;
    left: 10px;
}

.itemList label:hover{
    border: 1px solid #FB2F5D;
}

.itemList>input[type="checkbox"]:checked + label{
    border: 1px solid #FB2F5D;
}
.itemList input[type="checkbox"]:checked + label::before{
    box-sizing: border-box;
    font-size: 18px;
    content: "\2713";
    color: rgb(255,255,255);
    width: 20px;
    height: 20px;
    border: 1px solid #FB2F5D;
    border-radius: 50%;
    background: #FB2F5D;
    opacity: 0.8;
    display: inline-block;
}
.itemList>input[type="checkbox"]{
    visibility: hidden;
}
.itemList>button{
    background: transparent; 
    border: none; 
    outline: none;
}

.icon-delete-copy{
    font-size: 55px;
    color: #FF274D;
    opacity: 0.8;
    margin-right: -20px;
}
.icon-delete-copy:hover{
   color:#3B3838;
}

.UserDialog-Wrapper{ 
    position: fixed; 
    top: 0; 
    left: 0; 
    bottom: 0; 
    right: 0; 
    display: -webkit-flex; 
    display: flex; 
    -webkit-justify-content: center; 
            justify-content: center; 
    -webkit-align-items: center; 
            align-items: center;
  }
  .userDialog{
    border-radius: 4px;
    min-width: 20em;
    background: white;
    border: 1px solid #aaa;
    box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.userDialog > nav{
    font-size: 12px;
    height: 32px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.userDialog > .panel { 
    padding: 0 1em 1em; 
}

.userDialog form>.row{ 
    display: -webkit-flex; 
    display: flex; 
    -webkit-justify-content: space-around; 
            justify-content: space-around;
}

